.App {
  text-align: center;
}


.App-header {
  background-color: #222;
  margin-left:1.5rem;
  color: white;
}

.App-intro {
  font-size: large;
}

body {
      font-family: -apple-system, "BlinkMacSystemFont",
                  "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
                  "Fira Sans", "Droid Sans", "Helvetica Neue",sans-serif;
      
}
@media (min-width: 768px){
.container {
    width: auto;
}
}
.img-logo{
  height: 6rem;
  width: 15rem;
}
.headerContent {
      display: block;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      overflow: hidden;
      -o-text-overflow: ellipsis;
         text-overflow: ellipsis;
      color: black;
      font-weight: bold;
      text-decoration: none;
}
.headerContent:hover {
    color: black;
    text-decoration: none;
}
.clamping{
  -webkit-line-clamp: 2;
  overflow : hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  display: -webkit-box;
}
.description {
      overflow: hidden;
      -ms-text-overflow: ellipsis;
      -o-text-overflow: ellipsis;
      text-overflow: ellipsis;
      display: block;
      white-space: nowrap;
      display: block;
      display: -webkit-box;
      color: dimgray;
      max-height: 4rem;
    }
.labelAuthor {
      color: gray;
      cursor: none;
      font-size: 1.25rem;
    }
.list-group{
      margin-bottom: 5rem;
}
.navbar{
  margin-bottom: 0rem;
}




